<template>
    <div>
        <div class="search_tool">
            <div >
                <el-input style="width:300px;margin: 0px 10px 10px 0"
                          placeholder="请输入任务名称"
                          prefix-icon="el-icon-search"
                          v-model="searchValue.title"
                          @keydown.enter.native="initTasks"
                          clearable
                          @cleak="initTasks"
                          :disabled="showAdvanceSearchView">
                </el-input>
                <el-button icon="el-icon-search" type="primary" plain @click="initTasks" :disabled="showAdvanceSearchView" @keydown.enter.native="initTasks"></el-button>
                <el-button type="primary" @click="showAdvanceSearchView = !showAdvanceSearchView">
                    <i :class="showAdvanceSearchView?'fa fa-angle-double-up':'fa fa-angle-double-down'"
                       aria-hidden="true"></i>
                    高级搜索
                </el-button>
            </div>
            <div>
                <el-upload
                    :show-file-list="false"
                    :before-upload="beforeUpload"
                    :on-success="onSuccess"
                    :on-error="onError"
                    :disabled="importDataDisabled"
                    style="display: inline-flex;margin-right: 8px"
                    action="${base}/task/manage/import">
                    <el-button :disabled="importDataDisabled" type="success" :icon="importDataBtnIcon">
                        {{importDataBtnText}}
                    </el-button>
                </el-upload>
                <el-button type="success" @click="exportTask" icon="el-icon-upload2">导出数据</el-button>
<!--                <el-button icon="el-icon-plus" type="primary" plain style="margin-left: 10px" @click="addTask">添加任务</el-button>-->
            </div>
        </div>
            <div v-show="showAdvanceSearchView" class="show_tag">
                <el-row :gutter="25">
                    <el-col :span="5"><div class="grid-content bg-purple">
                        <el-select v-model="searchValue.proststus" placeholder="处理状态" size="samll" clearable>
                            <el-option
                                    v-for="item in proststus"
                                    :key="item.value"
                                    :label="item.lable"
                                    :value="item.value">
                            </el-option>
                        </el-select></div>
                    </el-col>
                    <el-col :span="5"><div class="grid-content bg-purple">
                        <el-select v-model="searchValue.priorities " placeholder="优先级" size="samll" clearable>
                            <el-option
                                    v-for="item in priorities"
                                    :key="item.value"
                                    :label="item.lable"
                                    :value="item.value">
                            </el-option>
                        </el-select></div>
                    </el-col>
                    <el-col :span="9"><div class="grid-content bg-purple">
                        <el-date-picker
                                v-model="searchValue.beginDateScope"
                                type="daterange"
                                size="samll"
                                unlink-panels
                                value-format="yyyy-MM-dd"
                                range-separator="至"
                                start-placeholder="开始日期"
                                end-placeholder="结束日期">
                        </el-date-picker></div>
                    </el-col>
                    <el-col :span="5" >
                        <div class="grid-content" style="display:flex;justify-content:start;margin-left: 10px">
                            <el-button size="small" icon="el-icon-search" type="primary" @click="initTasks">搜索</el-button>
                        </div>
                    </el-col>
                    <el-button size="small"  @click="cansel">取消</el-button>
                </el-row>
            </div>
        <div>
            <el-table  v-loading="loading"
                       element-loading-text="正在添加..."
                       element-loading-spinner="el-icon-loading"
                       element-loading-background="rgba(0, 0, 0, 0.8)"
                       :data="tasks"
                       :row-class-name="tableRowClassName"
                       border
                       style="width: 100%"
                       @selection-change="handleSelectionChange">
                <el-table-column
                        fixed=""
                        type="selection"
                        width="55">
                </el-table-column>
                <el-table-column
                        fixed=""
                        prop="id"
                        label="编号"
                        width="55">
                </el-table-column>
                <el-table-column
                        fixed=""
                        prop="title"
                        label="任务标题"
                        width="150">
                </el-table-column>
                <el-table-column
                        prop="priorityValue"
                        label="级别"
                        width="100">
                </el-table-column>
                <el-table-column
                        prop="processStatusValue"
                        label="状态"
                        width="100">
                </el-table-column>
                <el-table-column
                        prop="inputerName"
                        label="录入者"
                        width="100">
                </el-table-column>
                <el-table-column
                        prop="createDate"
                        label="创建时间"
                        width="200">
                </el-table-column>
                <el-table-column
                        prop="lastDate"
                        label="修改时间"
                        width="200">
                </el-table-column>
                <el-table-column label="操作" fixed="right" width="150">
                    <template slot-scope="scope">
                        <el-button
                                size="mini"
                                @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                        <el-button
                                size="mini"
                                type="danger"
                                @click="handleDelete(scope.$index, scope.row)">删除</el-button>
<!--                        <el-button-->
<!--                                size="mini"-->
<!--                                type="success"-->
<!--                                @click="handleDelete(scope.$index, scope.row)">详细信息</el-button>-->
                    </template>
                </el-table-column>
            </el-table>
            <div style="display: flex;justify-content: flex-end;margin-top: 10px">
                <el-pagination
                        :page-sizes=[1,10,20,30,50]
                        background
                        layout="sizes, prev, pager, next, jumper, ->, total, slot"
                        :total="total"
                        @current-change="currentChange"
                        @size-change="sizeChenge">
                </el-pagination>
            </div>
            <el-button size="small" type="danger" style="margin-top: 10px" :disabled="multipleSelection.length==0" @click="deleteTasks">
                批量删除
            </el-button>
        </div>
        <div style=""></div>
    </div>
</template>

<script>
import Processed from "../../components/task/Processed";
import Processing from "../../components/task/Processing";
import UnProcessed from "../../components/task/UnProcessed";


export default {
        name: "TaskManage",
        data(){
            return{
                activeName: '',
                title: "",
                tasks:[],
                multipleSelection: [],
                total: 0,
                pageNum: 1,
                pageSize: 10,
                loading: false,
                importDataDisabled: false,
                importDataBtnText: '导入数据',
                importDataBtnIcon: 'el-icon-download',
                importDataDisabled: false,
                showAdvanceSearchView: false,
                proststus: [],
                priorities: [],
                searchValue:{
                    proststus: '',
                    priorities: '',
                    beginDateScope: [],
                    title : ''
                }
            }
        },
        components:{
            UnProcessed,
            Processing,
            Processed
        },
        mounted() {
            this.initTasks();

        },
        methods:{
            tableRowClassName({row,rowIndex}){
                if(row.priority === "SALMON"){
                    return "low";
                }else if(row.priority === "TOMATO"){
                    return "midium";
                }else if(row.priority === "RED"){
                    return "high";
                }

            },
            exportTask(){
                let url = "${base}/task/manage/export?priorities=" + this.searchValue.priorities + "&proststus=" + this.searchValue.proststus
                    + "&title=" + this.searchValue.title
                if(this.searchValue.beginDateScope && this.searchValue.beginDateScope.length > 0){
                    url += "&beginDateScope=" + this.searchValue.beginDateScope
                }
               window.open(url,"_parent")
            },
            addTask(){
                this.$router.push("/task/taskPost")
            },
            sizeChenge(pageSize){
                this.pageSize = pageSize;
                this.initTasks();
            },
            currentChange(pageNum){
                this.pageNum = pageNum
                //console.log(this.pageNum)
                this.initTasks();
            },
            cansel(){
                this.showAdvanceSearchView = false;
                this.searchValue={
                        proststus: '',
                        priorities: '',
                        beginDateScope: [],
                        title : ''
                }
                this.initTasks()
            },
            deleteTasks(){
                this.$confirm('此操作将永久删除【'+this.multipleSelection.length+'】条记录, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    let ids = '';
                    this.multipleSelection.forEach(item =>{
                        ids += "ids="+item.id+'&'
                    })
                    this.deleteRequest("/task/manage/?"+ids).then(rsp=>{
                        if(rsp){
                            this.initTasks();

                        }
                    });

                    // this.$message({
                    //     type: 'success',
                    //     message: '注销成功!'
                    // });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消'
                    });
                });
            },
            // addTask(){
            //    // this.$router.push({path: '/editBlog', query: {from: this.activeName,id:row.id}})
            //     this.$router.push({path: '/task/postTask'});
            //
            // },
            handleEdit(index,data){
                console.log(data);
                this.$router.push({path: '/task/taskPost', query: {from:'update',id:data.id}});
            },
            onError(err, file, fileList) {
                this.importDataBtnText = '导入数据';
                this.importDataBtnIcon = 'el-icon-upload2';
                this.importDataDisabled = false;
            },
            onSuccess(response, file, fileList) {
                this.importDataBtnText = '导入数据';
                this.importDataBtnIcon = 'el-icon-upload2';
                this.importDataDisabled = false;
                this.initTasks();
            },
            beforeUpload() {
                this.importDataBtnText = '正在导入';
                this.importDataBtnIcon = 'el-icon-loading';
                this.importDataDisabled = true;
            },
            handleDelete(index,data){
                this.$confirm('此操作将永久删除该条记录, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.deleteRequest("/task/manage/"+data.id).then(rsp=>{
                        if(rsp){
                            this.initTasks();
                        }
                    });

                    // this.$message({
                    //     type: 'success',
                    //     message: '注销成功!'
                    // });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消注销'
                    });
                });
            },
            initTasks(){
                //this.loading = true;
                this.initPriorities();
                this.initProststus();
                let url = "/task/manage/?pageNum=" + this.pageNum + "&pageSize=" + this.pageSize + "&"
                        + "priorities=" + this.searchValue.priorities + "&proststus=" + this.searchValue.proststus
                        + "&title=" + this.searchValue.title
                if(this.searchValue.beginDateScope && this.searchValue.beginDateScope.length > 0){
                    url += "&beginDateScope=" + this.searchValue.beginDateScope
                }

                this.loading = true;
                return this.getRequest(url).then(rsp=>{
                    this.loading = false;
                    if(rsp){
                        this.tasks = rsp.data;
                        this.total = rsp.total;
                    }

                })
            },
            initPriorities(){
                this.getRequest("/task/manage/priorities").then(rsp=>{
                    if(rsp){
                        this.priorities = rsp
                    }

                })
            },
            initProststus(){
                this.getRequest("/task/manage/proStatus").then(rsp=>{
                    if(rsp){
                        this.proststus = rsp
                    }

                })
            },
            handleSelectionChange(val){
                this.multipleSelection = val;
            }
        },
    }


</script>

<style>
.search_tool{
    display: flex;
    justify-content: space-between;
}

.show_tag{
    border: 1px solid #409eff;
    border-radius: 5px;
    box-sizing: border-box;
    padding: 5px;
    margin: 15px 0px;
    display: flex;
    justify-content:space-between;
}

.el-row {
    margin-bottom: 0px;
    display: flex;
    /*justify-content: space-around;*/

}
.el-col {
    border-radius: 4px;
}
.bg-purple-dark {
    background: #99a9bf;
}
.bg-purple {
    background: #d3dce6;
}
.bg-purple-light {
    background: #e5e9f2;
}
.grid-content {
    border-radius: 4px;
    min-height: 36px;
}
.row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
}
.el-table .high {
    background: #ffb3aa;
}

.el-table .midium {
    background: #fff2ce;
}


.el-table .low {
    background: #f1f5fa;
}
</style>