<template>
  <div style="">
  <el-container v-loading="loading" class="post-article">
    <el-header class="header">
      <div style="margin-top: 10px">
        <el-input v-model="task.title" placeholder="请输入标题..." style="width: 300px;margin-right: 10px"></el-input>
        <el-select  v-model="task.priority" placeholder="请选择优先级" style="width: 150px;">
          <el-option
            v-for="item in priorities"
            :key="item.value"
            :label="item.lable"
            :value="item.value">
          </el-option>
        </el-select>
        <el-select  v-model="task.processStatus" placeholder="请选择状态" style="width: 150px;" v-show="show">
          <el-option
                  v-for="item in proststus"
                  :key="item.value"
                  :label="item.lable"
                  :value="item.value">
          </el-option>
        </el-select>
      </div>
    </el-header>

    <el-main class="main">
      <div id="editor">
        <mavon-editor style="height: 100%;width: 100%;" ref=md @imgAdd="imgAdd"
                      @imgDel="imgDel" v-model="task.mdContent"></mavon-editor>
      </div>
      <div style="display: flex;align-items: center;margin-top: 15px;justify-content: flex-end">
        <el-button @click="cancelEdit" v-if="from!=undefined">放弃修改</el-button>
        <template v-if="from==undefined || from=='draft'">
<!--          <el-button @click="saveBlog(0)">保存到草稿箱</el-button>-->
          <el-button type="primary" @click="saveBlog(-1)">发表文章</el-button>
        </template>
        <template v-else="from==post">
          <el-button type="primary" @click="updateTask">保存修改</el-button>
        </template>
      </div>
    </el-main>
  </el-container>
  </div>
</template>
<script>
  import {mavonEditor} from 'mavon-editor'
  import 'mavon-editor/dist/css/index.css'
  export default {
      name: "TaskPost",
      mounted: function () {
        this.initPriorities();
        this.initProststus();
      //this.getCategories();
        //console.log("<<<<<"+this.$route.query.from);
       this.from = this.$route.query.from;
      let _this = this;
      if(this.from){
            let id = this.$route.query.id;
            this.show = true;
            this.loading = true;
            this.getRequest("/task/manage/" + id).then(rsp=> {
              this.loading = false;
              if(rsp){
                this.task = rsp;
                this.task.processStatus= rsp.processStatusName;
                // console.log(this.task);
                // console.log(this.task.processStatus);
              }
            })
          }
       },
      components: {
        mavonEditor
      },
      methods: {
          cancelEdit(){
              this.$router.push("/task/manage")
              //this.$router.go(-2)
          },
          saveBlog(state){
              if (!this.task.title ||  !this.task.mdContent){
                this.$message.error('数据不能为空!');
                return;
              }
              let _this = this;
              _this.loading = true;
              //_this.task.mdContent =  _this.task.mdContent,
              _this.task.htmlContent = _this.$refs.md.d_render,
          this.postRequest("/task/manage/add", _this.task).then(rsp=> {
              _this.loading = false;
                  if (rsp) {
                    //console.log(rsp)

                    //this.cancelEdit();
                    this.$router.push("/task/manage")
                  }
              })
          },
        initPriorities(){
          this.getRequest("/task/manage/priorities").then(rsp=>{
            if(rsp){
              this.priorities = rsp
            }

          })
        },
        initProststus(){
          this.getRequest("/task/manage/proStatus").then(rsp=>{
            if(rsp){
              this.proststus = rsp
            }

          })
        },
        updateTask(){
          this.$confirm('此操作将修改该任务, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            if (!this.task.title ||  !this.task.mdContent){
              this.$message.error('数据不能为空!');
              return;
            }
            // let _this = this;
            // _this.loading = true;

            this.task.htmlContent = this.$refs.md.d_render,
            console.log(this.task);
            this.loading = true
            this.putRequest("/task/manage/updateTask", this.task).then(rsp=> {
              this.loading = false;
              if (rsp) {
                //this.cancelEdit();
                this.$router.push("/task/manage")
              }
            })

          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消注销'
            });
            this.$router.push("/task/manage")
          });

        },
        imgDel(pos){

        },
        getCategories(){
          let _this = this;
          this.getRequest("/admin/category/all").then(resp=> {
            _this.categories = resp.data;
          });
        },

        showInput() {
          this.tagInputVisible = true;
          this.$nextTick(_ => {
            this.$refs.saveTagInput.$refs.input.focus();
          });
        },
        handleInputConfirm() {
          let tagValue = this.tagValue;
          if (tagValue) {
            this.article.dynamicTags.push(tagValue);
          }
          this.tagInputVisible = false;
          this.tagValue = '';
        },
        imgAdd(pos, $file){
          let _this = this;
          // 第一步.将图片上传到服务器.
          let formdata = new FormData();
          formdata.append('image', $file);
          this.uploadFileRequest("/task/manage/uploadimg", formdata).then(rsp=> {

            if (rsp) {
              _this.$refs.md.$imglst2Url([[pos, rsp.obj]])
            } else {
              _this.$message({type: rsp.status, message: rsp.message});
            }
          });
        }
      },
      data() {
          return {
              priorities:'',
              proststus:'',
              tagInputVisible: false,
              tagValue: '',
              loading: false,
              from: '',
              task: {
                id: '-1',
                priority:'SALMON',
                title: '',
                processStatus:'UNPROCESSD',
                mdContent: '',
                htmlContent:''
              },
              show: false,
          }
      }
  }
</script>
<style>
  .post-article > .main > #editor {
    width: 100%;
    height: 450px;
    text-align: left;
  }

  .post-article > .header {
    background-color: #ececec;
    margin-top: 10px;
    padding-left: 5px;
    display: flex;
    justify-content: flex-start;
  }

  .post-article > .main {
    /*justify-content: flex-start;*/
    display: flex;
    flex-direction: column;
    padding-left: 5px;
    background-color: #ececec;
    padding-top: 0px;
  }

  .post-article > .header > .el-tag + .el-tag {
    margin-left: 10px;
  }

  .post-article > .header > .button-new-tag {
    margin-left: 10px;
    height: 32px;
    line-height: 30px;
    padding-top: 0;
    padding-bottom: 0;
  }

  .post-article > .header > .input-new-tag {
    width: 90px;
    margin-left: 10px;
    vertical-align: bottom;
  }

  .post-article {
  }
  .el-header .el-select{
   margin-right: 5px;
  }
</style>
